.header {
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}
.logo {
    width: 200px;
}
.nav-items {
    padding:0px 20px;
}

.nav-items > ul {
    font-size: 24px;
    display: flex;
    list-style-type: none;
}
.nav-items > ul > li{
    padding: 10px;
    margin: 10px;
}

.res-card {
    margin: 5px;
    padding: 5px;
    width:200px;
}

.res-container {
    display: flex;
    flex-wrap: wrap;
}

.res-card:hover {
    cursor: pointer;
    border: 1px solid black;
}

.res-logo {
    /* padding: 5px; */
    width: 100%;
    height: 200px;
}
.search {
    padding: 20px;
}

.filter-btn {
    margin: 10px;
}